/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
.wp-block-formello-form fieldset {
  padding: 10px;
  border: 1px solid black;
}
.wp-block-formello-form select {
  max-width: inherit;
}
.wp-block-formello-form input:disabled, .wp-block-formello-form input.disabled, .wp-block-formello-form select:disabled, .wp-block-formello-form select.disabled, .wp-block-formello-form textarea:disabled, .wp-block-formello-form textarea.disabled {
  background-color: #fff;
  border: 1px solid #7e8993;
  opacity: 1;
}
.wp-block-formello-form .block-editor-block-list__block {
  margin: 5px 0;
}
.wp-block-formello-form .formello {
  padding: 10px;
}
.wp-block-formello-form .formello small {
  padding: 0;
}

.formello-checkbox input {
  margin: 0;
}

.formello-hidden {
  display: flex;
  align-items: center;
}

.formello button {
  background: #abb8c3;
  color: #fff;
  border: 1px solid transparent;
  cursor: pointer;
  -webkit-appearance: button;
  padding: 10px 20px;
}

.cta {
  cursor: pointer;
  border: 1px solid;
}

.mailchimp {
  height: 50px;
  background-image: url(../assets/images/mailchimp.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}